.friendMessage {
    margin-bottom: 30px;

}

.friendImageAndText {
    display: flex;
    gap: 30px;
}

.friendImageAndText img {
    height: 48px;
    width: 48px;
}

.friendText {
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1), 0 5px 20px 0 rgba(29, 33, 38, 0.03);
}

.friendText:after {

}

.friendName {
    font-weight: 600;
    font-size: 16px;
}

.friendMessageText {
    font-weight: 400;
    font-size: 16px;
}

.friendTime {
    font-weight: 600;
    font-size: 10px;
    margin: 5px auto auto 10px;
}
