

.message {
margin-bottom: 30px;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    gap: 30px;

}

.imageAndText img {
    height: 48px;
    width: 48px;
}

.text {
    background-color: #0066CC;
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1), 0 5px 20px 0 rgba(29, 33, 38, 0.03);
}

.text:after {

}

.name {
    font-weight: 600;
    font-size: 16px;
    text-align: end;
}

.messageText {
    font-weight: 400;
    font-size: 16px;
}

.time {
    font-weight: 600;
    font-size: 10px;
    text-align: end;
    margin-right: 10px;
    margin-top: 5px;
}